import React from 'react';
import Home from './components/Pages/Home/Home';
import Login from './components/Pages/Login/Login';
import PageNotFound from './components/Pages/PageNotFound/PageNotFound';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import GreetingPage from './components/Pages/GreetingPage/GreetingPage';

const App = () => {
  return (
    <BrowserRouter basename='Project_DATN'>
      <Routes>
        {/* t trang Login lm trang mc nh */}
        <Route path="/" element={<GreetingPage />} />

        {/* Cu hnh ng dn c th cho trang Home */}
        <Route path="/home" element={<Home />} />
        <Route path="/login" element={<Login />} />

        {/* Route ny s bt tt c cc ng dn khng xc nh v hin th PageNotFound */}
        <Route path="*" element={<PageNotFound />} />
      </Routes>
    </BrowserRouter>
  );
};

export default App;
